
import { takeEvery, call, put, delay } from "redux-saga/effects";
import { getMovies } from "./api";
import { fetchMovies, fetchMoviesSuccess, fetchMoviesError } from "./moviesSlice";

function* fetchMoviesHandler({ payload: moviesId }) {
    try { 
        yield delay(1000);
        const movies = yield call(getMovies, moviesId);
        yield put(fetchMoviesSuccess(movies));
    } catch (error) {
        yield put(fetchMoviesError(error));
    }
}

export function* moviesSaga() {
    yield takeEvery(fetchMovies.type, fetchMoviesHandler);
}