import { createSlice } from '@reduxjs/toolkit';

const moviesSlice = createSlice({
    name: 'movies',
    initialState: {
        movies: {},
    },
    reducers: {

        fetchMovies: (state) => {
            state.loading = true;
        },
        fetchMoviesSuccess: (state, { payload: movies }) => {
            state.loading = false;
            state.movies = movies;
        },
        fetchMoviesError: (state) => {
            state.loading = false;
        },
    },
});

export const {
    fetchMovies,
    fetchMoviesSuccess,
    fetchMoviesError
} = moviesSlice.actions;

const selectMovies = state => state.movies;
const selectPeople = state => state.people;

export const getMoviesById = (state, movieId) => selectMovies(state).find(({ id }) => id === movieId);
export const getPeopleById = (state, peopleId) => selectPeople(state).find(({ id }) => id === peopleId);

export default moviesSlice.reducer;